.header-wrapper {
    text-align: center;
    padding-bottom: 30px;
}

.header-wrapper p,
.wrapper p {
    line-height: 12px;
    font-size: 16px;
}

.header-wrapper,
.wrapper {
    width: 100%;
    height: auto;
    margin: 0 auto;
    background-color: #fff;
    line-height: 12px;
}

.ticket-wrapper-body {
    border: 2px #ccc solid;
    margin-left: 30px;
}

.page-title {
    color: rgb(11, 243, 30);
}

.passenger {
    padding: 10px 20px;
    border: 2px #000 solid;
    margin-bottom: 10px;
}

.passenger-item {
    display: flex;
    justify-content: space-between;
}

.passenger-item p {
    color: #000;
    text-transform: uppercase;
}

.ticket-header {
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    text-transform: uppercase;
    padding: 15px;
    border-bottom: 2px #000 solid;
    width: 100%;
}

.ticket-segment-items {
    width: 100%;
    height: auto;
}

.ticket-segment {
    width: 45%;
    height: 160px;
    border: 2px #ccc solid;
    float: left;
    text-align: left;
    margin-top: 20px;
    padding: 10px;
}

.ticket-layover {
    width: 10%;
    height: 160px;
    border-top: 2px #ccc solid;
    border-bottom: 2px #ccc solid;
    float: left;
    text-align: left;
    text-align: center;
    margin-top: 20px;
    padding-top: 50px;
}

.departure-box {
    width: 45%;
}

.departure-box p {
    font-size: 12px;
}

.flight-box {
    width: 10%;
    text-align: center;
    margin-left: -15px;
    margin-right: 15px;
}

.flight-box p {
    font-size: 12px;
}

.destination-box {
    width: 45%;
}

.destination-box p {
    font-size: 12px;
}

.segment-box {
    overflow: hidden;
    float: left;
}

.segment-box p {
    line-height: 12px;
}


.ticket-condition-header {
    padding: 5px 0;
    color: #000;
    font-weight: bold;
}

.ticket-condition-body {
    color: #000;
    text-align: left;
    padding-top: 20px;
}

.ticket-condition-body p {
    font-size: 18px;
    line-height: 20px;
}