.package-heading-content {
    position: relative;
}

.package-meta {
    display: flex;
    justify-content: space-between;
    padding: 10px 15px;
    background: #272016;
}

.package-rating {
    ul{
        li {
            display: inline-block;
            color: #fa9e1b;
            margin-right: 10px;
        }
        li:last-child {
            margin-right: 0;
        }
    }
}


.package-price{
    .price {
        color: #fff;
        font-size: 19px;
        font-weight: 500;
    }
} 

.package-thumbnail{
    position: relative;
    overflow: hidden;
    img {
        transition: all .3s linear;
        overflow: hidden;
        position: relative;
        width: 255px;
        height: 200px;
    }
}

.package-detail-thm {
    width: 255px;
}

.package-wrapper-content:hover {
    .package-thumbnail{
        img {
            transform: scale(1.1);
        }
    }
    .package-publish-date {
        opacity: 1;
        visibility: visible;
    }
    h2.package-title{
        a {
            opacity: 1;
            visibility: visible;
        }
    }
}

.package-publish-date {
    position: absolute;
    top: 0;
    background: #fa9e1c;
    opacity: 0;
    visibility: hidden;
    transition: all .3s linear;
    span {
        display: inline-block;
        padding: 5px 10px;
        color: #fff;
    }
}

h2.package-title{
    a {
        color: #3a3a3a;
        text-transform: capitalize;
        font-weight: 400;
        display: inline-block;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        margin-top: 0;
        color: #fafafa;
        left: 50%;
        opacity: 0;
        visibility: hidden;
        transition: all .3s linear;
    }
}
