.service-area {
    background: url("../../images/packageBg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    position: relative;
}

.service-area:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, .60);
}

.single-service-content {
    padding: 35px 20px;
    background: #fff;
    position: relative;
    border-radius: 4px;
    text-align: center;
    position: relative;
    top: 0;
    transition: all .2s ease-in-out;
    margin-top: 15px;
}

.single-service-content:hover {
    top: -10px;
}

.single-service-inner-content{
    margin-top: 18px;
    h3 {
        line-height: 30px;
        font-size: 17px;
        margin-bottom: 10px;
    }
    a {
        margin-top: 20px;
    }
    a:hover {
        color: #fff;
    }
}

.service-box {
    position: absolute;
    top: -21px;
    left: 50%;
    transform: translateX(-50%);
    height: 50px;
    width: 50px;
    background: #fe9738;
    text-align: center;
    border-radius: 4px;
}

.service-box i {
    color: #fff;
    line-height: 50px;
    font-size: 25px;
}

.package-book-now {
    display: inline-block;
    background: #fe9738;
    padding: 10px 30px;
    border-radius: 4px;
    color: #fff;
}