img {
    max-width: 100%;
    height: auto;
}
#page-container {
    min-height: calc(100vh - 300px);
}

p {
    padding: 0;
    margin: 0;
    font-size: 14px;
    color: #3a3a3a;
    font-family: sans-serif;
    line-height: 27px;
    font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: sans-serif;
    color: #3a3a3a;
    margin: 0 0 26px;
}

h1 {
    font-size: 42px;
    line-height: 54px;
    font-weight: 600;
}

h2 {
    font-size: 36px;
    line-height: 46px;
    font-weight: 600;
}

h3 {
    font-size: 26px;
    line-height: 36px;
    font-weight: 500;
}

h4 {
    font-size: 20px;
    line-height: 30px;
    font-weight: 500;
}

h5 {
    font-size: 18px;
    line-height: 28px;
    font-weight: 500;
}

h6 {
    font-size: 16px;
    line-height: 26px;
    font-weight: 600;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    color: #fe9738;
    transition: all 0.3s ease 0s;
    -webkit-transition: all 0.3s ease 0s;
    -ms-transition: all 0.3s ease 0s;
    outline: none !important;
}

a:active,
a:hover {
    text-decoration: none;
    outline: 0 none;
    color: #fe9738;
}

.bg-primary {
    background: #004487;
}

.link-color {
    color: #5b9bd1;
}

.link-color:hover {
    opacity: 0.7;
}

.hidden {
    overflow: hidden;
}

.cursor-pointer {
    cursor: pointer;
}

.text-bold {
    font-weight: bolder;
}

::-moz-selection {
    background: #fe9738;
    text-shadow: none;
    color: #ffffff;
}

::selection {
    background: #fe9738;
    text-shadow: none;
    color: #ffffff;
}

@-webkit-keyframes autofill {
    to {
        color: inherit;
        background: transparent;
    }
}

input:-webkit-autofill {
    -webkit-animation-name: autofill;
    -webkit-animation-fill-mode: both;
}

input[type="submit"] {
    cursor: pointer;
}

.mt-50 {
    margin-top: 50px;
}

.mt-100 {
    margin-top: 100px;
}

.white-color {
    color: #fff;
}

.relative {
    position: relative;
    overflow: hidden;
}

.section-padding {
    padding: 95px 0;
}

.section-padding-top {
    padding-top: 130px;
    padding-bottom: 50px;
}

.row-center {
    align-items: center;
}

.section-title{
    justify-content: space-between;
    h2{
        position: relative;
        text-transform: uppercase;
        padding-bottom: 15px;
        display: inline-block;
    }
    h2::after {
        content: "";
        position: absolute;
        bottom: 1px;
        left: 0;
        width: 100%;
        height: 1px;
        background: #fe9738;
    }
    h2::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 45px;
        background: #1b2f3c;
        height: 3px;
        z-index: 1;
    }
    a {
        background-color: #343a40;
        color: #fff;
        margin: 10px 5px;
        padding: 5px;
        text-align: center;
        font-weight: bold;
        border-radius: 3px;
        text-decoration: none;
        width: calc((100% - 160px) / 8);
        float: left;
    }
    a:last-child {
        width: calc((100% - 160px) / 5);
    }
    a:hover {
        background-color: #28a745;
        color: #fff !important;
        text-decoration: none;
    }
    a.active {
        background-color: #28a745 !important;
        color: #fff !important;
    }
}
.color-title {
    h2 {
        position: relative;
        text-transform: uppercase;
        padding-bottom: 15px;
        display: inline-block;
        color: #fff;
    }
    h2::after {
        content: "";
        position: absolute;
        bottom: 1px;
        left: 0;
        width: 100%;
        height: 1px;
        background: #fe9738;
    }
    h2::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 45px;
        background: #fe9738;
        height: 3px;
        z-index: 1;
    }
}


.btn-pointer {
    cursor: pointer;
}

.loading-area {
    text-align: center;
}

.link-btn {
    cursor: pointer;
}

.link-btn:hover {
    opacity: 0.7;
}
form{
    .form-group{
        label{
            span {
                color: red;
            }
        }
    }
}
.page-preloader-wrapper{
    width: 100%;
    height: 100%;
    background: #6c6c6c7d;
    position: fixed;
    left: 0;
    top: 0;
    .page-preloader {
        margin: auto;
        transform: translate(-50%, -50%);
        top: 40%;
        left: 50%;
        position: absolute;
    }
}