.input-form-area {
    padding: 30px;
    background: linear-gradient(to right, #fa9e1b, #8d4fff);
    border-radius: 4px;
    input.form-control {
        background: #fff;
        color: #000;
        height: 40px;
    }
    label.form-label {
        color: #fff;
    }
    p {
        color: #fff;
    }
    a {
        color: rgb(40, 19, 226);
    }
}

.btn-dark.disabled,
.btn-dark:disabled {
    cursor: no-drop;
}

.section-title.text-center{
    .section-title-padding{
        img {
            max-width: 180px;
            border-radius: 50%;
        }
    }
}