.flight-show-section {
    background-color: #f4f4f4;
    border: none;
    padding: 5px 10px;
    margin-bottom: 15px;
}

.air-segment-ref-area{
    .departure-image {
        height: 30px;
    }
}

.create-reservation-area{
    input.e-ticketing-input-field {
        height: 35px;
    }
}

select.e-ticketing-input-field {
    width: 100%;
    height: 35px;
    border-radius: 4px;
    border: none;
    margin-bottom: 11px;
    padding: 0 10px;
    font-size: 16px;
}


.flight-result-heading {
    font-weight: 600;
    border: 2px green solid;
    height: 30px;
    text-align: left;
    line-height: 30px;
    margin: 5px 0;
    font-size: 16px;
    color: #000080;
    padding-left: 5px;

    .col-lg-12{
        div {
            float: left;
        }
    }
    .airline {
        width: 12%;
    }
    .departure {
        width: 23%;
    }
    .destination {
        width: 16%;
    }
    .arrival {
        width: 25%;
    }
}

.flight-result-section {
    border: 1px #ccc solid;
    padding: 5px 15px;
    color: #000;
    margin-bottom: 10px;
    font-size: 14px;
}

.duration-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 100px;
}

.transit {
    float: left;
    text-align: center;
    color: #000080;
    width: 100%;
    div {
        font-size: 12px;
    }
    b {
        margin-left: -15px;
    }
    svg {
        float: left;
        height: 30px;
    }
}

.transit-airport-code {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 2px #f00 solid;
    text-align: center;
    line-height: 29px;
    font-size: 10px;
    float: left;
}

.biman-logo {
    max-height: 30px;
    height: auto;
}

.air-manage-btn-section {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;

    .button-area {
        flex-basis: 55%;
        height: 100%;
        font-size: 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        position: relative;
    }
    .popicon {
        color: #0a1357;
        font-size: 1rem;
        height: 2rem;
        padding: 2px 4px;
        width: auto;
        position: relative;
    }
    .price-area {
        flex-basis: 20%;
        color: #000080;
        font-size: 20px;
        text-align: center;
        border: 1px solid #ddd;
        margin-right: 15px;
        padding: 2px 5px;
        border-radius: 5px;
        font-weight: 600;
    }
    .book-btn-area {
        flex-basis: 20%;
        font-size: 20px;
        text-align: center;
        margin-right: 5px;
    }
}

.refund-icon {
    border: 1px #ccc solid;
    overflow: hidden;
    vertical-align: middle;
    font-size: 30px;
    background: #ffffff;
    width: 28px;
    height: 28px;
    line-height: 23px;
    text-align: center;
    cursor: pointer;
    color: red;
    border-radius: 3px;
    margin-right: 2px;
    img {
        padding: 3px;
    }
}

.flight-total-time {
    font-weight: 600;
    margin-bottom: 0;
    margin-top: -10px;
}

.air-manage-btn-section{
    .button-area{
        svg {
            border: 1px #ccc solid;
            overflow: hidden;
            vertical-align: middle;
            font-size: 30px;
            background: #ffffff;
            width: 25%;
            text-align: center;
            cursor: pointer;
            border-radius: 3px;
            margin-right: 2px;
        }
    }
}

.transit-section {
    background: #eeee;
    padding: 2px 5px;
    margin: 10px 0;
    overflow: hidden;
    padding-top: 5px;
}

.transit-biman-logo{
    img {
        max-height: 31px;
    }
}