.top-header{
    width: 100%;
    height: 30px;
    background-color: #000;
    color: #fff;
    line-height: 30px;
    li{
        padding: 0 5px;
        a{
            text-decoration: none;
            color: #fff;
        }
    }
    li:hover{
        opacity: 0.7;
        cursor: pointer;
    }
}