    color: #797979;
    font-family: sans-serif;
    vertical-align: baseline;
    line-height: 27px;
    background: #ffffff;
    overflow-x: hidden;
    height: 100%;
    margin: 0;
}

blockquote {
    padding: 25px 35px;
    margin-left: 30px;
    background: #f5f5f5;
    margin: 30px 0 30px 30px;
    box-shadow: -5px 0 #232323;
}

blockquote p:last-child {
