/* banner style */

.home .overlay {
    background: rgba(27, 47, 60, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    height: 100vh;
    z-index: 9;
}

.home {
    height: 100vh;
    position: relative;
}

.home-slider {
    position: absolute;
    left: 0px;
    top: 0px;
    height: 100%;
    width: 100%;
    z-index: 9;
}

.display-table {
    width: 100%;
    height: 100%;
    display: table;
}

.table-cell {
    display: table-cell;
    vertical-align: middle;
}

.banner-info {
    height: 100vh;
    min-height: 100%;
}

.banner-info h4 {
    position: relative;
    padding-left: 68px;
    color: #fe9738;
    font-weight: 400;
}

.banner-info h4:before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    width: 53px;
    height: 1px;
    background: #fe9738;
}

.banner-meta {
    display: block;
    margin: 0px 0 28px;
    padding-left: 74px;
    line-height: 16px;
}

.banner-meta li {
    display: inline;
    position: relative;
    text-transform: uppercase;
    font-size: 14px;
    color: #ddd;
    font-weight: 700;
    line-height: 14px;
    letter-spacing: 0.5px;
}

.banner-meta li:first-of-type::before {
    top: -1px;
    content: " ";
    font-size: 11px;
    position: relative;
    color: currentcolor;
    padding: 0 20px 0 0;
}

.banner-info h1 {
    color: #fff;
    white-space: nowrap;
    text-transform: capitalize;
    font-weight: 700;
    line-height: 80px;
    position: relative;
    font-size: 70px;
}

.banner {
    position: relative;
    z-index: 33;
}

.arrow-down {
    bottom: 40px;
    left: 50%;
    position: absolute;
    z-index: 11;
    -webkit-animation: animate 3s infinite;
    -moz-animation: animate 3s infinite;
    -ms-animation: animate 3s infinite;
    -o-animation: animate 3s infinite;
    animation: animate 3s infinite;
}

@keyframes animate {
    0% {
        -webkit-transform: translate(0px, 0px);
        -moz-transform: translate(0px, 0px);
        -ms-transform: translate(0px, 0px);
        -o-transform: translate(0px, 0px);
        transform: translate(0px, 0px);
    }

    50% {
        -webkit-transform: translate(0px, 0px);
        -moz-transform: translate(0px, 20px);
        -ms-transform: translate(0px, 20px);
        -o-transform: translate(0px, 20px);
        transform: translate(0px, 20px);
    }

    100% {
        -webkit-transform: translate(0px, 0px);
        -moz-transform: translate(0px, 0px);
        -ms-transform: translate(0px, 0px);
        -o-transform: translate(0px, 0px);
        transform: translate(0px, 0px);
    }
}

.arrow-down>a {
    color: #fff;
    display: inline-block;
    font-size: 27px;
    line-height: 31px;
    transition: all .3s linear;
}

.arrow-down>a:hover {
    color: #fe9738;
}


