.short-padding {
    padding: 60px 0 0;
}

.footer-social-content li {
    display: inline-block;
    margin-right: 10px;
}

.footer-social-content li a {
    display: inline-block;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    text-align: center;
    line-height: 40px;
    background: #fe9738;
    color: #fff;
}

.widget-inner-content p {
    color: #fff;
}

.widget-inner-content a {
    color: #fff;
    display: block;
    padding: 5px 0;
}

.single-address {
    display: flex;
    margin-bottom: 11px;
}

.address-icon {
    height: 30px;
    width: 30px;
    flex-shrink: 0;
}

.address-icon i {
    color: #fff;
}

.widget-title h3 {
    color: #fe9738;
}

.footerbg {
    background: #007bff;
}

.footer-wrapper a img {
    margin-bottom: 5px;
}

.footer-bottom {
    background: #000;
    padding: 20px 0;
}

p.copy-right-text {
    color: #fff;
}

form.subscribe-form {
    position: relative;
}

input.subscribe-field {
    width: 100%;
    height: 50px;
    border-radius: 4px;
    background: #fff;
    border: none;
    padding: 0 14px;
    color: #3a3a3a;
}

button.sub-btn.btn {
    position: absolute;
    top: 0;
    height: 100%;
    padding: 0 20px;
    right: 0px;
    background: #fe9738;
    border: none;
    border-radius: 0 4px 4px 0 !important;
}

button.sub-btn.btn i {
    color: #fff;
    font-size: 22px;
}

.flight-root-area {
    text-align: right;
    /* background-color: #4f5458; */
    width: 250px;
    height: 45px;
    float: right;
}

.flight-root-area span {
    float: left;
    color: #fff;
    line-height: 45px;
    width: 33.33%;
    text-align: center;
    cursor: pointer;
    background-color: #4f5458;
}

.flight-root-area span.active {
    background-color: #004487;
}
