
.contact-area {
    background: url("../../images/formBg.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    position: relative;
}

.contact-area:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, .60);
}

.e-ticket-foem .e-ticket-form-field input {
    height: 50px;
    text-transform: capitalize;
    font-size: 16px;
    color: #fff;
    background: transparent;
    border-left: 0;
    border-top: 0;
    border-right: 0;
    border-bottom: 1px solid #fff;
    width: 100%;
    margin-bottom: 18px;
}

.e-ticket-foem .e-ticket-form-field input:focus {
    outline: none;
}

.e-ticket-form-field textarea {
    width: 100%;
    height: 200px;
    background: transparent;
    border-left: 0;
    border-right: 0;
    border-top: 0;
    border-bottom: 1px solid #fff;
    color: #fff;
}

.e-ticket-form-field input[type="text"]::-webkit-input-placeholder {
    color: #fff;
}

.e-ticket-form-field input[type="email"]::-webkit-input-placeholder {
    color: #fff;
}

.e-ticket-form-field textarea::placeholder {
    font-size: 16px;
    color: #fff;
}

.e-ticket-form-field textarea:focus {
    outline: none;
}

input.submit-btn.btn {
    background: #fe9738;
    color: #fff;
    padding: 10px 20px;
    text-transform: capitalize;
}
